</template>
<script>
export default {
    data(){
        return{
            exc_f6:true,
            row:5,
            col:9,
            curr_userid:'',
            check_date:'',
            open_time_table:false,
            open_list_apply:false,
            swap_cls_list:[],
            original_day:'',
            original_session:'',
            empty_swap_lesson:[],
            record_id:'',
            reply_wrapper:false,
            apply_day:'',
            apply_session:'',
            ava_date_list:[],
            daysess:'',
            inner_teacher:'',
            inner_day:'',
            inner_session:'',
            inner_period:'',
            inner_class:'',
            inner_location:'',
            inner_subject:'',
            x_margin:['22.5%','38.2%','53.9%','36.5%','51.2%','66.9%'],
            inner_margin:'',
            css_cls:[],
            dp_period:['1','2','3','4','5','6','7','8','9'],
            ready_change:false,
            by_day:['1','2','3','4','5','6'],
            day_list:['','','','','',''],
            order:'day',
            blk:false,
            ava_lesson_list:[],
            ava_session_list:[],
            sel_sup:false,
            tmp_date:'',
            message:[],
            swap_loc:[],
            swap_sub:[],
            swap_date:[],
            swap_day:[],
            swap_session:[],
            old_session:'',
            original_date:'',
            cycle_date:[],
            swap_subject_code:'',
            loc_list:[],
            sup1:'',
            sup2:'',
            sup3:'',
            app_type:'swap',
            selected_cls:'',
            selected_loc:'',
            subject_list:[],
            subject_code:'',
            type:'official',
            user_check:'',
            ava_list:[],
            select_day:'',
            select_session:'',
            target_date:'',
            open_list:false,
            tdy_date:'',
            style:[],
            tdy_day:'',
            lesson:[],   //format : lesson[session][day]
            subject:[],
            location:[],
            class_a:[],
            swap_id:[],
            ready:false,
            uidata:[],
            ui_sub:[],
            swap_teacher_list:[],
            swap_teacher_list_dp:[],
        }
    },
    watch: {
      tdy_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       },
       tmp_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       }
    },   
    async created(){
        await this.get_userinfo();
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.tdy_date = today;
        this.check_date = today;
        await this.check_sub_date();
        var i = 0;
        var j = 0;
        for(i=0;i<this.col;i++){
            this.swap_teacher_list[i] = [];
            this.empty_swap_lesson[i] = [];
            this.message[i] = [];
            this.style[i] = [];
            this.lesson[i] = [];
            this.subject[i] = [];
            this.location[i] = [];
            this.class_a[i] = [];
            this.css_cls[i] = [];
            this.swap_id[i] = [];
            for(j=0;j<this.col;j++){
                this.swap_teacher_list[i][j] = [];
                this.empty_swap_lesson[i][j] = [];
                this.css_cls[i][j] = 'other_lesson';
                this.message[i][j] = '';
                this.style[i][j] = 'pointer-events:none';
                this.lesson[i][j] = '';
                this.subject[i][j] = '';
                this.location[i][j] = '';
                this.class_a[i][j]='';
                this.swap_id[i][j]='';
            }
        }
        await this.fetch_cycle();
        await this.fetch_subject();
        await this.fetch_loc();
        await this.fetch_timetable();
        //await this.check_overlay();
        await this.new_day();
        await this.check_url();
        this.ready_change = true;  
        this.ready = true;        
    },
    async mounted(){
 
    },
    methods:{
        fetch_exc_f6_data(){
            this.exc_f6 = !this.exc_f6;
            this.new_day();        
        },
        async check_url(){
            var swap_date = document.getElementById('swap_date').value;
            if(swap_date!=''){
                this.tdy_date = swap_date;
                await this.new_day();
            }
        },
        async get_userinfo(){
            await axios.get('../permission.php')
                .then(
                    (response)=>{
                        this.curr_userid = response.data[0].username;
                        console.log(this.curr_userid);
                    }
                )
        },
        async check_overlay(){
            var i = 0;            
            for(i=0;i<this.row;i++){
                var formData = new FormData();
                formData.append('cycle_date',this.cycle_date[i]);
                await axios.post('subsitude/sub/backend/fetch_leave.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                var n=0;
                                var k=0; //day
                                var s=0; //session
                                var l=0;
                                for(l=0;l<response.data.length;l++){
                                    if(this.order=='day'){
                                        k = response.data[l].day;
                                        s = response.data[l].session;
                                    } else if(this.order=='date'){
                                        for(n=0;n<this.row;n++){
                                            if(response.data[l].date == this.cycle_date[n]){
                                                k = n+1;
                                            }
                                        }
                                        s = response.data[l].session;
                                    }
                                    this.subject[k-1][s] = response.data[l].subject;
                                    this.class_a[k-1][s] = response.data[l].class;
                                    this.location[k-1][s] = response.data[l].location;
                                    this.message[k-1][s] = '';//'Leave type : '+response.data[l].app_type+' with '+response.data[l].assigned_id;
                                    if(response.data[l].app_type=='sup'){
                                        this.style[k-1][s] = 'background-color:rgb(56, 201, 114);color:rgba(255,255,255,0.8);pointer-events:none';
                                    } else if((response.data[l].app_type=='swap')&&(response.data[l].action=='passive')){
                                        if((response.data[l].stat=='pending')&&(this.curr_userid==response.data[l].leave_id)){
                                            this.style[k-1][s] = '';
                                            this.css_cls[k-1][s] = 'reply';
                                        } else if((this.curr_userid==response.data[l].assigned_id)&&(response.data[l].stat!='accepted')){
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(227,132,23,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        } else if((this.curr_userid==response.data[l].assigned_id)&&(response.data[l].stat=='accepted')){
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(255,0,0,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        } else {
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(29,112,207,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        }
                                    } else if((response.data[l].app_type=='swap')&&(response.data[l].action=='active')){
                                        if((response.data[l].stat=='pending')&&(this.curr_userid==response.data[l].assigned_id)){
                                            this.style[k-1][s] = '';
                                            this.css_cls[k-1][s] = 'reply';
                                        } else if((this.curr_userid==response.data[l].assigned_id)&&(response.data[l].stat!='accepted')){
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(227,132,23,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        } else if((this.curr_userid==response.data[l].assigned_id)&&(response.data[l].stat=='accepted')){
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(255,0,0,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        } else {
                                            this.css_cls[k-1][s] = 'other_lesson';
                                            this.style[k-1][s] = 'background-color:rgba(29,112,207,0.85);color:rgba(255, 255, 255, 0.85);pointer-events:none';
                                        }
                                    }
                                }
                            }
                        }
                    )
            }
        },
        async fetch_cycle(){
            await axios.get('subsitude/sub/backend/fetch_cycle.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.tdy_day = response.data[0].day;
                        var tmp_day = Number(this.tdy_day)-1;
                        var i = 0;
                        var clr ='';
                        for(i=0;i<this.col;i++){                     
                            if(i%2 == 0){
                                clr = 'rgba(145, 255, 156, 0.75)';
                            } else {
                                clr = 'rgba(178, 255, 186, 0.75)';
                            }
                            this.style[tmp_day][i] = 'cursor:pointer;background-color:'+ clr;
                        }
                    }
                }
            )
        },
        async fetch_subject(){
            await axios.get('subsitude/sub/backend/fetch_subject.php')
            .then(
                (response)=>{
                    this.subject_list = response.data;
                    this.subject_code = this.subject_list[0].subject_code;
                }
            )
        },
        async fetch_loc(){
            await axios.get('subsitude/sub/backend/fetch_loc.php')
            .then(
                (response)=>{
                    this.loc_list = response.data;
                }
            )
        },
        async fetch_timetable(){
            var formData = new FormData();
            formData.append("exc_f6",this.exc_f6);
            axios.post('subsitude/sub/backend/fetch_time_table.php',formData)
            .then(
                (response)=>{
                    var i = 0;
                    var j = 0;
                    var counter = 0;
                    for(i=0;i<this.row;i++){
                        for(j=0;j<this.col;j++){
                            this.lesson[i][j] = response.data[counter].subject;
                            if(this.lesson[i][j]==''){
                                this.check_sub_record(i,j);
                            } else {
                                this.class_a[i][j] = response.data[counter].class;
                                this.subject[i][j] = response.data[counter].subject;
                                this.location[i][j] = response.data[counter].classroom;
                            }        
                            var check_les = this.class_a[i][j];
                            if(this.exc_f6 && check_les.indexOf("6")>-1){
                                this.lesson[i][j] = response.data[counter].subject;
                                this.class_a[i][j] = '';
                                this.subject[i][j] = '';
                                this.location[i][j] = '';
                            }                           
                            counter++;
                        }
                    }
                }
            )  
        },
        async fetch_day(i){
            var date = this.swap_date[i];
            var old_date = this.tmp_date;
            var formData = new FormData();
            formData.append('date',this.swap_date[i]);
            await axios.post('subsitude/sub/backend/new_day.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='empty'){
                            this.$alert('Selected Date is not a cycle Day !');
                            this.swap_date[i] = old_date;
                            this.$forceUpdate();
                        } else {
                            this.swap_day[i] = response.data[0].day;
                            this.tmp_date = this.swap_date[i];
                            this.$forceUpdate();
                        }
                    }
                )
        },
        async new_day(){
            var tmp = '';
            if(this.order=='day'){                
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                await axios.post('subsitude/sub/backend/new_day.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                tmp = '';
                                this.tdy_day = response.data[0].day;
                                var tmp_day = Number(this.tdy_day)-1;
                                var i = 0;
                                var clr ='';
                                var n = 0;
                                var m = 0;
                                var ptevent = '';
                                for(n=0;n<this.row;n++){
                                    for(m=0;m<this.col;m++){
                                        this.style[n][m] = 'pointer-events:none';
                                        this.css_cls[n][m] = 'hvlesson';
                                    }
                                }
                                for(i=0;i<this.col;i++){                     
                                    if(i%2 == 0){
                                        clr = 'rgba(145, 255, 156, 0.75)';
                                    } else {
                                        clr = 'rgba(178, 255, 186, 0.75)';
                                    }
                                    if(this.lesson[tmp_day][i]==''){
                                        //ptevent = 'pointer-events:none;';
                                        ptevent = 'cursor:pointer;';
                                        this.css_cls[tmp_day][i] = 'other_lesson';
                                    } else {
                                        ptevent = 'cursor:pointer;';                                      
                                    }
                                    this.style[tmp_day][i] = ptevent+'background-color:'+ clr;
                                }                                                      
                            } else {
                                tmp = 'empty'
                                this.$alert('Selected Date is not a cycle Day !');
                                this.tdy_date = this.original_date;
                                /*
                                var k=0;
                                var l=0;
                                for(k=0;k<this.col;k++){
                                    for(l=0;l<this.col;l++){
                                        this.style[k][l] = 'pointer-events:none';
                                    }
                                    
                                }*/
                            }
                        }
                    )
                    if(tmp!='empty'){
                        await this.check_sub_date();
                        await this.reload_timetable();
                        await this.check_overlay();  
                    }
                    this.$forceUpdate();
            }
            if(this.tdy_date!=this.check_date){
                this.open_time_table = true;
            }
        },
        async reload_timetable(){
            if(this.order=='day'){
                await axios.get('subsitude/sub/backend/fetch_time_table.php')   //day
                .then(
                    (response)=>{
                        var i = 0;
                        var j = 0;
                        var counter = 0;
                        for(i=0;i<this.row;i++){
                            for(j=0;j<this.col;j++){
                                this.lesson[i][j] = response.data[counter].subject;
                                if(this.lesson[i][j]==''){
                                    this.check_sub_record(i,j);
                                } else {
                                    this.message[i][j]='';
                                    this.class_a[i][j] = response.data[counter].class;
                                    this.subject[i][j] = response.data[counter].subject;
                                    this.location[i][j] = response.data[counter].classroom;
                                }         
                                var check_les = this.class_a[i][j];
                                if(this.exc_f6 && check_les.indexOf("6")>-1){
                                    this.lesson[i][j] = response.data[counter].subject;
                                    this.class_a[i][j] = '';
                                    this.subject[i][j] = '';
                                    this.location[i][j] = '';
                                }                     
                                counter++;
                            }
                        }
                    }
                )
            }
        },
        close_overlay(){
            this.open_list = false;
            this.open_list_apply = false;
        },
        close_overlay_reply(){
            this.reply_wrapper = false;
        },
        leave_type(){
            this.old_session = this.select_session;
            if(this.type=='sick'){              
                if(this.select_session!='Full day'){
                    this.old_session = this.select_session;
                    this.select_session = 'Full Day';
                }
                //this.app_type ='sup';
            } else {
                this.select_session = this.old_session;
                this.app_type = 'swap';
            }
        },
        async check_sub_date(){
            var formData = new FormData();
            formData.append('date',this.tdy_date);
            await axios.post('subsitude/sub/backend/fetch_cycle_date.php',formData)
                .then(
                    (response)=>{
                        var i = 0;
                        for(i=0;i<response.data.length;i++){
                            if(!(response.data == 'No cycle day')){
                                if(!(response.data[i]==undefined)||!(response.data[i]==null)){
                                    var index = response.data[i].day;
                                    index = Number(index)-1;
                                    this.cycle_date[index] = response.data[i].date;
                                }
                            }
                        }
                    }
                )
        },
        async check_sub_record(i,j){
            var formData = new FormData();
            formData.append('cycle_date',this.cycle_date[i]);
            formData.append('day',(i+1));
            formData.append('session',j);
            await axios.post('subsitude/sub/backend/check_sub_info.php',formData)
                .then(
                    (response)=>{
                        if(response.data.length>0){
                            this.message[i][j] ='';//response.data[0].app_type + ' - ( From : ' + response.data[0].leave_id + ')';
                            this.lesson[i][j] = response.data[0].subject;
                            this.subject[i][j] = response.data[0].subject;
                            this.class_a[i][j] = response.data[0].class;
                            this.location[i][j] = response.data[0].location;
                            this.css_cls[i][j] = 'request';
                            this.style[i][j] = '';
                        } else {
                            this.css_cls[i][j] = 'other_lesson';
                            this.message[i][j] = '';
                            this.lesson[i][j] = '';
                            this.subject[i][j] = '';
                            this.class_a[i][j] = '';
                            this.location[i][j] = '';
                        }
                    }
                )
            this.$forceUpdate();
        },
        sel_mode(i,j){
            if(this.css_cls[i][j]=='ava_lesson'){                        
                this.check_swap_date(i,j);
            } else if(this.css_cls[i][j]=='reply'){
                this.request_check(i,j);
            } else if(this.css_cls[i][j]=='other_lesson'){
                this.fetch_cls_less(i,j);
            } else if(this.css_cls[i][j]=='hvlesson'){
                this.apply_day = i+1;
                this.apply_session = j;     
                this.checkswap(i,j);
            } else if(this.css_cls[i][j]=='ret_swap'){
                this.fetch_comb(i,j);
            }
        },
        async fetch_cls_less(i,j){
            await this.new_day();
            this.style[i][j]='background-color:rgba(160, 237, 255,0.8)';
            this.message[i][j] = 'Selected';
            this.original_day = i;
            this.apply_day = i;
            this.original_session = j;
            this.apply_session = j;
            var formData = new FormData();
            formData.append('swap_date',this.cycle_date[i]);
            formData.append('day',(i+1));
            formData.append('session',j);
            formData.append("exc_f6",this.exc_f6);
            await axios.post('subsitude/sub/backend/check_cls_lesson.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            var x=0;
                            var y=0;
                            var z=0;
                            var day_data = [];
                            var session_data = [];
                            var msg = '';
                            var tmp_teacher_name='';
                            for(x=0;x<this.row;x++){
                                day_data = response.data[x];    
                                if(response.data[x]!=undefined){                      
                                    for(y=0;y<this.col;y++){
                                        z=0;
                                        if(day_data[y]!=undefined){
                                            session_data = day_data[y];
                                            msg='';
                                            for(z=0;z<session_data.length;z++){
                                                this.swap_teacher_list[x][y][z] = [];
                                                this.swap_teacher_list[x][y][z]['teacher_id'] = session_data[z].teacher; 
                                                this.swap_teacher_list[x][y][z]['teacher_name'] = session_data[z].teacher_name;    
                                                tmp_teacher_name = session_data[z].teacher_name;                     
                                            }
                                            this.message[x][y] = tmp_teacher_name;
                                            this.css_cls[x][y] = 'ret_swap';
                                            this.style[x][y] = 'cursor:pointer';
                                            //console.log('Day:'+(x+1)+' Period:'+y+' Class:'+msg);
                                        }
                                    }
                                }
                            }   
                            this.$forceUpdate();                   
                        } else {
                            this.$alert('No swap pair available');
                        }
                    }
                )
        },
        async fetch_comb(i,j){
            this.inner_margin = "margin-left:"+this.x_margin[i];
            await this.assign_cls(i,j);
            this.open_list_apply = true;
        },
        async assign_cls(i,j){
            this.swap_teacher_list_dp = this.swap_teacher_list[i][j];
            this.inner_teacher  = this.swap_teacher_list[i][j][0].teacher_id;
            this.inner_class = this.class_a[i][j];
            var day=i+1;
            this.inner_day = day;
            this.inner_session = j;
            var session = j;
            this.original_date = this.cycle_date[i];
            var formData = new FormData();
            formData.append('day',day);
            formData.append('teacher',this.swap_teacher_list[i][j][0].teacher_id);
            formData.append('session',session);
            formData.append('class',this.inner_class);
            formData.append('date',this.cycle_date[i]);
            await axios.post('subsitude/sub/backend/fetch_lesson_swap.php',formData)
                .then(
                    (response)=>{
                        this.ava_date_list = response.data[0];
                        this.inner_subject = response.data[0].subject;
                    }
                )
        },
        async change_cls(){
            var day=this.inner_day
            var session = this.inner_session;
            var formData = new FormData();
            formData.append('day',day);
            formData.append('class',this.inner_class);
            formData.append('teacher',this.swap_teacher_list[i][j][0].teacher_id);
            formData.append('session',session);
            formData.append('date',this.original_date);
            await axios.post('subsitude/sub/backend/fetch_lesson_swap.php',formData)
                .then(
                    (response)=>{
                        this.ava_date_list = response.data[0];
                        this.inner_subject = response.data[0].subject;
                    }
                )
        },
        async accept_swap(){
            var formData = new FormData();
            formData.append('id',this.record_id);
            await axios.post('subsitude/swap_view/backend/accept_req.php',formData)
                .then(
                    (response)=>{

                        this.$alert("Successfully Accepted Swap");
                        this.reply_wrapper = false;
                        this.new_day();                      
                    }
                )
        },
        async decline_swap(){
            var formData = new FormData();
            formData.append('id',this.record_id);
            await axios.post('subsitude/swap_view/backend/decline_req.php',formData)
                .then(
                    (response)=>{
                        this.$alert("Successfully Declined Swap");
                        this.reply_wrapper = false;
                        this.new_day();                      
                    }
                )

        },
        async request_check(i,j){
            this.inner_margin = "margin-left:"+this.x_margin[i];
            var formData = new FormData();
            formData.append('swap_date',this.cycle_date[i]);
            formData.append('day',(i+1));
            formData.append('session',j);
            await axios.post('subsitude/sub/backend/check_req.php',formData)
                .then(
                    (response)=>{
                            this.record_id = response.data[0].id;
                            this.inner_day = response.data[0].day;
                            this.inner_session = response.data[0].session;    
                            this.inner_teacher = response.data[0].name;
                            this.inner_class = response.data[0].class;
                            this.inner_subject = response.data[0].old_subject+" → "+response.data[0].subject;                    
                    }
                )
            this.reply_wrapper = true;
        },
        async check_swap_date(i,j){
            var formData = new FormData();           
            var day = this.by_day[i];
            var session = j;           
            formData.append('date',this.cycle_date[i]);
            formData.append('day',day);
            formData.append('session',session);
            formData.append('class',this.class_a[i][j])
            formData.append('teacher',this.swap_id[i][j]);
            await axios.post('subsitude/sub/backend/fetch_ava_day.php',formData)
                .then(
                    (response)=>{
                        this.ava_date_list = response.data;
                    }
                )
            this.inner_margin = "margin-left:"+this.x_margin[i];
            this.inner_teacher = this.message[i][j];
            this.inner_class = this.class_a[i][j];
            this.inner_subject = this.subject[i][j];
            this.inner_day = day;
            this.inner_session = session;
            this.open_list = true;
        },
        async checkswap(i,j){
            var hasrecord = false;
            if((this.lesson[i][j]!='Blk2')&&(this.lesson[i][j]!='Blk3')){
            await this.new_day();
            var session = j;           
            var day = this.by_day[i];
            this.app
            this.select_day = day;
            this.select_session = session;
            var formData = new FormData();
            formData.append('date',this.cycle_date[i]);
            formData.append('session',session);
            formData.append('day',day);
            formData.append('class',this.class_a[i][j]);
            formData.append('location',this.location[i][j]);
            formData.append('mode',1);
            await axios.post('subsitude/sub/backend/swap_info_ui.php',formData)
                .then(
                    (response)=>{
                        var x = 0;  //day
                        var y = 0;  //record
                        var s = 0;
                        var day = '';
                        var period = '';
                        var can_swap = false;
                        for(x=0;x<this.row;x++){
                            y=0;
                            this.uidata[x] = response.data[x];
                            this.ui_sub = this.uidata[x];
                            for(y=0;y<this.uidata[x].length;y++){                              
                                if(this.ui_sub[y].name.length>0){                                  
                                    s = this.ui_sub[y].session;
                                    day = this.ui_sub[y].day-1;
                                    period = this.ui_sub[y].session;
                                    this.lesson[day][period] = this.ui_sub[y].subject;
                                    this.subject[day][period] = this.ui_sub[y].subject;
                                    this.class_a[day][period] = this.ui_sub[y].class;
                                    this.location[day][period] = this.ui_sub[y].classroom;
                                    this.message[day][period] = this.ui_sub[y].name;
                                    this.swap_id[day][period] = this.ui_sub[y].username;
                                    this.style[day][period] = "";
                                    this.css_cls[day][period] = 'ava_lesson';
                                    can_swap = true;
                                }
                            }
                        }
                        if(can_swap==false){
                            this.$alert('No swap pair available');
                        }
                        this.$forceUpdate();
                    }
                )
            } else {
                this.$alert('Blk lesson cannot be swapped');
            }
        },
        async new_list(){
            var i = Number(this.select_day)-1;
            var j = this.select_session;
            var formData = new FormData();
            formData.append('sel_date',this.tdy_date);
            formData.append('session',this.select_session);
            formData.append('day',this.select_day);
            formData.append('app_type',this.app_type);
            formData.append('class',this.class_a[i][j]);
            await axios.post('subsitude/sub/backend/fetch_ava_teacher.php',formData)
                .then(
                    (response)=>{
                        this.ava_list = response.data;
                    }
                )
            this.$forceUpdate();
        },
        handleSubmit(){
            var formData = new FormData();
            var d = this.inner_day-1;
            var s = this.inner_session;
            formData.append('swap_day',this.inner_day);
            formData.append('swap_session',this.inner_session);
            formData.append('swap_location',this.location[d][s])
            formData.append('apply_day',this.apply_day);
            formData.append('apply_session',this.apply_session);
            formData.append('class',this.inner_class);
            formData.append('subject',this.inner_subject);
            formData.append('userid',this.swap_id[d][s]);
            formData.append('swap_date',this.swap_date);
            formData.append('apply_date',this.tdy_date);            
            axios.post('subsitude/sub/backend/apply_swap_v2.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="Successfully Submitted Application"){
                            this.$alert(response.data);
                            this.close_overlay(); 
                            this.new_day();                     
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        },
        submit_rep_swap(){
             var formData = new FormData();
            var d = this.inner_day-1;
            var s = this.inner_session;
            formData.append('swap_day',this.inner_day);
            formData.append('swap_session',this.inner_session);
            formData.append('swap_location',this.location[d][s])
            formData.append('apply_day',this.apply_day+1);
            formData.append('apply_session',this.apply_session);
            formData.append('class',this.inner_class);
            formData.append('subject',this.inner_subject);
            formData.append('userid',this.inner_teacher);
            formData.append('swap_date',this.swap_date);
            formData.append('apply_date',this.tdy_date);            
            axios.post('subsitude/sub/backend/rep_swap.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="Successfully Submitted Application"){
                            this.$alert(response.data);
                            this.close_overlay(); 
                            this.new_day();                     
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
.other_lesson {
    pointer-events: none!important;
}
th{
    width:15.7%;
    text-align: center;
    font-size: 1.1rem;
}
td{
    padding-left:1.25rem;
    padding-right:1.25rem;
    border:2px solid #ffffff;
    height:5rem;   
    transition: all 0.3s ease-in-out;
}
td:hover{
    background-color:rgba(160, 237, 255,0.8)!important;
}
.ret_swap{
    background-color:rgb(53, 82, 181)!important;
    color:rgba(255,255,255,0.85)!important;
    cursor:pointer!important;
}
.ret_swap:hover{
    background-color:rgb(12, 51, 180)!important;
    color:rgba(255,255,255,0.85)!important;
}
.inner_overlay{
    z-index: 5;
    width:22.5rem; /*15.7% width for each column, + 7.3%margin-left*/
    max-height:60%;
    position:absolute;
    margin-left:23%;
    margin-top:10%;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.sub_tr{
    height:5rem!important;
}
.ava_lesson{
    background-color:rgba(255,0,0,0.85);
    color:rgba(255,255,255,0.85);
    cursor:pointer;
}
.ava_lesson:hover{
    background-color:rgb(150,65,66)!important;
}
.darkred{
    background-color:rgb(150,65,66)!important;
}
.out_wrapper{
    position:relative;
}
.reply{
    background-color:rgba(129, 0, 209,0.85);
    color:rgba(255, 255, 255, 0.85);
    cursor:pointer;
}
.reply:hover{
    background-color:rgb(87, 0, 140)!important;
}
.request{
    background-color:rgba(255,0,0,0.85);
    color:rgba(255, 255, 255, 0.85);
    pointer-events:none;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.legend_item{
    pointer-events:none!important;
    font-weight: bold;
    width:10%;
    margin:0 0.25% 0 0.25%;
    text-align: center;
    font-size:0.975rem;
    color:rgba(255,255,255,0.85);
}
.legend_item:last-of-type{
    margin-right:0.05rem!important;
}
</style>



